<template>
    <div class="login">
        <div class="login-background">
        </div>
        <div class="login-logo-container">
            <div class="login-logo-img"></div>
        </div>
        <form class="login-box-container" @submit.prevent="onSubmit">
          <van-panel class="panel-style">
            <div slot="header">
                <div class="login-box" ref="loginbox">
                    <div class="login-box-title">登录友山基金运营平台</div>
                    <van-field class="login-box-username" placeholder="请输入用户名" v-model="username">
                    </van-field>
                    <van-field type="password" v-model="password" class="login-box-password" placeholder="密码" ref="password">
                    </van-field>
                    <van-button :disabled="!formFilled" :type="computedTip.type" class="login-box-submit" @click="handleLogin">{{computedTip.tip}}</van-button>
                </div>
            </div>
          </van-panel>
        </form>
    </div>
</template>
<script>
import { Button, Field, Panel, Toast } from "vant";
import { login, saveLogin } from "../../utils/auth";
export default {
  components: {
    [Button.name]: Button,
    [Field.name]: Field,
    [Panel.name]: Panel,
    [Toast.name]: Toast
  },
  data() {
    return {
      username: "",
      password: "",
      errorMessage: "未知错误",
      stayLogin: false
    };
  },
  methods: {
    onSubmit(){
      return false;
    },
    handleLogin() {
      if (this.formFilled) {
        const toast = Toast.loading({
          duration: 0, // 持续展示 toast
          forbidClick: true, // 禁用背景点击
          loadingType: "spinner",
          message: "登录中…"
        });
        // toast.clear();
        // this.$router.push("/manage/laborCosts");
        login(this.username, this.password).then(
          succ => {
            toast.clear();
            // this.$store.dispatch("GetUserInfo");
            window.location = this.$route.query.redirect || "/manage/laborCosts";
            saveLogin(succ.token);
          },
          err => {
            toast.clear();
            this.handleError(err);
            
          }
        );
      }
    },
    handleError(data) {
      this.$toast(data.message || "发生了不明错误!!!");
    }
  },
  computed: {
    formFilled() {
      return this.username && this.password ? true : false;
    },
    computedTip() {
      if (!this.username || this.username.length == 0) {
        return {
          tip: "请输入用户名",
          type: "warning"
        };
      } else if (!this.password || this.password.length == 0) {
        return {
          tip: "请输入密码",
          type: "warning"
        };
      } else {
        return {
          tip: "登陆",
          type: "primary"
        };
      }
    }
  },
  mounted() {
    if (this.$route.query.logout) {
      this.handleError({ message: "已经安全退出" });
    }
  }
};
</script>

<style>
.login {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  top: 0;
  left: 0;
}
.login-background {
  background-position: center center;
  background-size: cover;
  filter: brightness(0.4) saturate(2);
  -ms-filter: brightness(0.4) saturate(2);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-image: url(../../static/login-background.jpg);
}

.login-logo-container {
  width: 8rem;
  z-index: 2;
  padding-bottom:.3rem; 
  display: flex;
  align-items: flex-end;
  justify-content: center;
  flex: auto;
}

.login-logo-container > * {
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

.login-logo-img {
  height: 2rem;
  width: 8rem;
  border: 0.2rem solid transparent;
  background-image: url(../../static/logo-img-text-white.png);
}
.login-box-container {
  flex: auto;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  z-index: 2;
  display: flex;
}
.login-box-title {
  color: rgba(0, 0, 0, 0.5);
  font-size: .4rem;
  text-align: center;
  padding-bottom: .5rem;
}

.login-box {
  display: flex;
  flex-direction: column;
  padding: .3rem;
  box-sizing: border-box;
  width: 8rem;
}
.login-box > .login-box-password {
  position: relative;
  padding-top: .3rem;
}
</style>

